<template>
  <div v-if="movie">
    <v-card  class="mx-auto my-12" max-width="600">
      <template slot="progress">
        <v-progress-linear color="deep-purple" height="10" indeterminate></v-progress-linear>
      </template>

      <v-img height="250" contain v-bind:src="movie.Poster"></v-img>

      <v-card-title>{{movie.Title}}</v-card-title>

      <v-card-text>
        <v-row align="center" class="mx-0">
          <v-rating :value="parseFloat(movie.imdbRating)" 
          half-increments
          color="amber" dense readonly :length="10" size="14"></v-rating>

          <div class="grey--text ms-4">{{movie.imdbRating}}</div>
          <v-dialog v-model="dialog" width="500">
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="red lighten-2" dark v-bind="attrs" v-on="on">View Rating</v-btn>
            </template>
            <v-card>
              <v-card-title class="text-h5 grey lighten-2">Rating and Review</v-card-title>
              <v-card-text>
                <v-card v-for="review in movie.Ratings" :key="review.Source" class="p-3 my-3">
                  <h6>{{review.Source}}</h6>
                  <p>{{review.Value}}</p>
                </v-card>
              </v-card-text>
            </v-card>
          </v-dialog>
        </v-row>

        <div class="my-4 text-subtitle-1">{{movie.Actors}}</div>

        <div>{{movie.Plot}}</div>
      </v-card-text>

      <v-card-text>
        <v-chip-group active-class="deep-purple accent-4 white--text" column>
          <v-chip v-for="genre in movie.Genre.split(',')" :key="genre">{{genre}}</v-chip>
        </v-chip-group>
      </v-card-text>

    
    </v-card>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: "Detail",
  mounted() {
    let imdbId = this.$route.params.imdbId;
    fetch(`http://www.omdbapi.com/?i=${imdbId}&apikey=87d10179`)
      .then(response => response.json())
      .then(data => {
        console.log(data);
        this.movie = data;
      });
  },
  data() {
    return {
      movie: null,
      dialog: false
    };
  }
};
</script>
